@import url("https://fonts.googleapis.com/css2?family=Rowdies:wght@300;400;700&display=swap");

* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  background-color: #f7f9fc;
}

html {
  overflow: hidden;
}

.nav {
  width: 100vw;
  display: flex;
  justify-content: center;
  background: linear-gradient(135deg, #f0e5ff, #d9f3fa, #f0ffda);
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
  padding: 20px 0;
  border-radius: 12px;
  position: sticky;
  top: 0;
  z-index: 100;
}

.nav ul {
  display: flex;
  align-items: center;
  gap: 2.5rem;
}

img {
  width: 20px;
  height: 20px;
  opacity: 0;
  transition: 200ms ease;
}

.nav ul li {
  font-size: 20px;
  font-weight: 600;
  font-family: "Arial", sans-serif;
  position: relative;
  padding: 10px 20px;
  transition: transform 0.3s ease;
  display: flex;
  gap: 0.5rem;
  &:hover {
    transform: scale(1.1);

    img {
      opacity: 1;
    }
  }
}

.nav ul li.active {
  background: linear-gradient(135deg, #dbceef60, #bec9cb60, #ddefc260);
  border-radius: 10px;

  img {
    opacity: 1;
  }
}

.nav ul li a {
  color: #4a4a4a;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: color 0.3s ease;
}

.nav ul li a:hover {
  color: #7a6cff;
}

h1 {
  text-align: center;
  font-family: "Rowdies", sans-serif;
  font-weight: 600;
  font-style: normal;
  font-size: 72px;
}

.home h1 {
  background: -webkit-linear-gradient(#c2b5a1, #8b6f5b, rgb(255, 0, 0));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  padding: 80px 0;
}

.about h1 {
  background: -webkit-linear-gradient(#3d3224, #eea362, rgb(30, 30, 171));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  padding: 80px 0;
}

.contact h1 {
  padding: 80px 0;
  background: -webkit-linear-gradient(#b7e0d7, #4a7f6d, rgb(213, 213, 26));

  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

@media (max-width: 515px) {
  html {
    font-size: 20%;
  }

  .nav ul li {
    font-weight: 500;
    padding: 10px 5px;
  }
}
